html {
  background-color: #fafbfd;
}

body {
  color: #464242;
  margin: 0;
  padding: 0;
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: 18px;
}

h1 {
  text-align: center;
  font-size: 2rem;
}

h2 {
  text-align: center;
  font-size: 1.8rem;
}

h3 {
  font-size: 1.4rem;
}

.saltar-aqui a {
  color: #0248b1;
  position: absolute;
  font-size: .4rem !important;
}

header {
  border-bottom: 2px solid #4a7c0e;
  width: 100%;
  max-width: 800px;
  margin: 0% auto;
  padding: 10px;
}

.header-nav {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0% auto;
  display: flex;
}

.img-logo {
  max-width: 150px;
}

header a {
  color: #0248b1;
  border-radius: 5px;
  margin-right: 20px;
  padding: 5px;
  font-size: 1.4rem;
  font-weight: 700;
  text-decoration: none;
}

header a:hover {
  color: #a30021;
}

footer {
  background-color: #464242;
  margin-top: 50px;
  padding-top: 40px;
  padding-bottom: 40px;
}

footer p {
  text-align: center;
  color: #fff;
  margin: 0;
  font-size: 12px;
}

footer a {
  color: #fff;
}

.ref-icon {
  margin-top: 10px;
}

main {
  width: 100%;
  max-width: 800px;
  margin: 0% auto;
  padding: 10px;
}

p {
  text-align: left;
  word-spacing: .16em;
  margin-bottom: 2em;
  font-size: 1rem;
  line-height: 1.5;
}

.img-portada {
  border-radius: 10px;
  width: 100%;
