  font-weight: 700;
}

.caja-direccion a:hover {
  color: #a30021;
}

.caja-eco-iconos {
  text-align: center;
}

.caja-eco-iconos img {
  margin-left: 40px;
}

blockquote {
  text-align: justify;
  background-color: #f3f3f3;
  border: 2px solid #464242;
  border-radius: 10px;
  padding: 20px;
}

blockquote p {
  margin: 0;
}

.footer-cite {
  text-align: right;
  background-color: #f3f3f3;
  border-radius: 10px;
  margin: 0;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: 40px;
}

.lista {
  text-align: center;
}

.lista img {
  transform-origin: center;
  border-radius: 10px;
  width: 40%;
  transition: transform .6s;
}

.lista img:hover {
  transform: scale(2);
}

.info-distancia {
  text-align: center;
  margin: 0 !important;
}

.caja-ver-mas {
  text-align: right;
  margin-top: 0;
}

.enlace-ver-mas {
  color: #0248b1;
  padding-right: 50px;
}

.enlace-ver-mas:hover {
  color: #a30021;
}

hr {
  border: none;
  border-top: 1px solid #ebebeb;
}

.img-representativa {
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  height: 250px;
}

.fotos-galeria {
  justify-content: center;
  max-width: 800px;
  display: flex;
}

.fotos-galeria img {
  border-radius: 10px;
  width: 225px;
  height: 225px;
  margin: 25px;
}

.fotos-galeria img:hover {
  transform: scale(2);
}

iframe {
  border: none;
  border-radius: 10px;
  width: 100%;
  height: 500px;
}

#map {
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  height: 400px;
}

.pag-contactos {
  place-items: center;
  height: 100vh;
}

.contactos {
  text-align: center;
  background-color: #f3f3f3;
  border: 1px solid #464242;
  border-radius: 10px;
  padding: 20px;
}

.contactos p {
  text-align: center;
  margin: 0;
}

@media (width <= 900px) {
  main {
    max-width: 900px;
  }

  header {
    border-bottom: 2px solid #4a7c0e;
    width: 100%;
    max-width: 900px;
  }

  .header-nav {
    flex-direction: column;
  }

  .header-nav nav {
    text-align: center;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    display: flex;
  }

  .header-nav nav a {
    color: #0248b1;
    font-size: 1.5rem;
    text-decoration: underline;
  }

